.icon .fa:before {
  background:
    /*linear-gradient(to left, rgba(65, 88, 208, 1) 0%, rgba(200, 80, 192, 1) 46%, rgba(255, 204, 112, 1) 100%);*/
    -webkit-gradient(linear, left top, right top, from(#FEE140), color-stop(50%, #fc4083));
  background:
    /*linear-gradient(to left, rgba(65, 88, 208, 1) 0%, rgba(200, 80, 192, 1) 46%, rgba(255, 204, 112, 1) 100%);*/
    -webkit-linear-gradient(left, #FEE140 0%, #fc4083 50%);
  background:
    /*linear-gradient(to left, rgba(65, 88, 208, 1) 0%, rgba(200, 80, 192, 1) 46%, rgba(255, 204, 112, 1) 100%);*/
    -o-linear-gradient(left, #FEE140 0%, #fc4083 50%);
  background:
    /*linear-gradient(to left, rgba(65, 88, 208, 1) 0%, rgba(200, 80, 192, 1) 46%, rgba(255, 204, 112, 1) 100%);*/
    linear-gradient(90deg, #FEE140 0%, #fc4083 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

