header {
  height: 100vh;
  min-height: 350px;
  background-color: #21D4FD;
  background:
      -webkit-linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      -webkit-linear-gradient(135deg, rgba(65, 88, 208, .35) 0%, rgba(200, 80, 192, .35) 46%, rgba(255, 204, 112, .35) 100%),
      url('img/background.jpg') no-repeat;
  background:
      -o-linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      -o-linear-gradient(135deg, rgba(65, 88, 208, .35) 0%, rgba(200, 80, 192, .35) 46%, rgba(255, 204, 112, .35) 100%),
      url('img/background.jpg') no-repeat;
  background:
      linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      linear-gradient(315deg, rgba(65, 88, 208, .35) 0%, rgba(200, 80, 192, .35) 46%, rgba(255, 204, 112, .35) 100%),
      url('img/background.jpg') no-repeat;

  background-size: cover;
  text-align: center;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
}

.text {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}



